<template>
  <el-col :span="10">
    <div id="form_two">
      <div class="back_rc">
        <div class="form_two_div">
          <div v-if="ok_ln">
            <h1>文章</h1>
            <el-divider></el-divider>
           <span v-if="ok_timeline">
              <el-timeline :reverse="false">
              <el-timeline-item
                  v-for="(mds, index) in mds"
                  :key="index"
                  :timestamp="mds.time"
                  @click="gethtml(index)">
                  <el-link :underline="false" style="font-size:18px">{{mds.title}}</el-link>&nbsp;
                  <el-tag effect="plain" class="mx-1" v-for="tags in mds.tags" :key="tags" size="small" round>{{tags}}</el-tag>
              </el-timeline-item>
            </el-timeline>
           </span>
            <span v-if="!ok_timeline">
            <el-timeline :reverse="false">
              <el-timeline-item
                  v-for="classifymds in classifymd"
                  :key="classifymds"
                  :timestamp="classifymds.time"
                  @click="gethtml(classifymds.index)"
                  >
                <el-link :underline="false" style="font-size:18px">{{classifymds.title}}</el-link>&nbsp;
                <el-tag effect="plain" class="mx-1" v-for="tags in classifymds.tags" :key="tags" size="small" round>{{tags}}</el-tag>
              </el-timeline-item>
            </el-timeline>
            </span>
          </div>
          <div v-if="ok_book">
            <el-link :underline="false" type="primary" @click="gettf"  style="font-size:16px;color:#F56C6C">
              <b><el-icon><Back /></el-icon>返回</b>
            </el-link>
            <p style="font-size: 30px">{{html.title}}</p>
            <p style="font-size: 15px;color: #909399">
              <el-icon><User /></el-icon>
              Sail
              <el-divider direction="vertical" />
              <el-icon><Calendar /></el-icon> {{html.time}}
              <el-divider direction="vertical" />
              <el-icon><Folder /></el-icon>分类:
              <el-tag effect="plain" type="info" class="mx-1" v-for="tags in html.tags" :key="tags">{{tags}}</el-tag>
            </p>
            <el-divider />
            <div class="markdown-body" v-html="html.html"></div>
          </div>
        </div>
      </div>
    </div>
  </el-col>
  <el-col :span="3">
    <div id="form_three">
      <div class="back_rc">
        <div class="form_three_div">
          <div class="div1"><p>最近文章</p></div>
          <div class="div2">
            <ul style="list-style-type: none;">
              <template v-for="(mds, index) in mds.slice(0,6)" :key="index">
                <li>
                  <el-link :underline="false" @click="gethtml2(index)">{{mds.title}}</el-link>
                </li>
              </template>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div id="form_three2">
      <div class="back_rc">
        <div class="form_three_div">
          <div class="div1" @click="this.ok_timeline=true"><p>分类目录</p></div>
          <div class="div2">
            <ul style="list-style-type: none;">
              <template v-for="tags in options.slice(0,6)" :key="tags">
                <li>
                  <el-link :underline="false" @click="setclassify(tags.tag)">{{tags.tag}}</el-link>
                </li>
              </template>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </el-col>
</template>

<script>
import '../assets/css/github-markdown.min.css'

export default {
  data() {
    return {
      mds:'',
      classifymd:[],
      html:'',
      options:'',
      ok_ln: true,
      ok_book: false,
      ok_timeline: true,
      url:'http://demo.qzhai.net/gohan/wp-content/uploads/2020/01/stock-photo-1005217204-1-100x100.png'
    }
  },
  mounted() {
    this.getmds()
    this.gettags()
  },
  methods: {
    setclassify(tag){
      this.classifymd = []
      this.ok_timeline = true
      for (var i in this.mds){
        if (this.mds[i].tags[0] == tag){
          var json = this.mds[i]
          json['index'] = i
          this.classifymd.push(json)
          if (this.classifymd!==[]){
            this.ok_timeline = false
          }
        }
      }
    },
    gettags(){
      this.axios.get('http://124.220.157.4:8080/tagsRead')
          .then((response) => {
            this.options = response.data.tags
          })
    },
    getmds() {
      this.axios.get('http://124.220.157.4:8080/mdRead')
          .then((response) => {
            this.mds = response.data.mds
          })},
    gethtml(inx) {
      this.html = this.mds[inx]
      this.gettf()
    },
    gethtml2(inx) {
      this.html = this.mds[inx]
      this.ok_ln = false
      this.ok_book = true
    },
    gettf(){
      this.ok_book = !this.ok_book
      this.ok_ln = !this.ok_ln
    },
  }
}
</script>
