  margin-top: 30px;
}

#form_three {
  margin-top: 30px;
  padding-left: 15px;
}

#form_three2 {
  margin-top: 15px;
  padding-left: 15px;
}
.form_three_div .div1 {
  color: #606266;
  padding-top: 3px;
  padding-left: 35px;
  border-bottom: 1px solid var(--el-border-color);
}

